@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;700&family=Roboto:wght@100;400&display=swap);


:root {
    --black-basic: rgba(0, 0, 0, 1);
    --black-modal-layer: rgba(0, 0, 0, 0.55);
    --box-shadow-basic: rgba(0, 0, 0, 0.4) 0px 2px 4px,
        rgba(0, 0, 0, 0.3) 0px 7px 13px -3px, rgba(0, 0, 0, 0.2) 0px -3px 0px inset;
    --box-shadow-btn-mobile: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px;
    --green-basic: rgba(1, 150, 33, 1);
    --grey-card-shadow: rgba(204, 204, 204, 1);
    --red-dark: rgba(252, 47, 40, 1);
    --transition-data: 0.25s linear;
    --white-basic: rgba(254, 255, 255, 1);
    --yellow: rgba(255, 203, 70, 1);
}

html {
    scroll-behavior: smooth;
}

* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

ul,
ol {
    list-style: none;
}

a {
    color: inherit;
    text-decoration: none;
}

button {
    border: none;
    cursor: pointer;
    outline: 1px solid transparent;
}

input {
    border: none;
    outline: 1px solid transparent;
    width: 100%;
}

body {
    background: var(--yellow);
    font-family: "Montserrat", sans-serif;
}



.calendar__section--form {
    background: var(--white-basic);
    border-bottom-right-radius: 40px;
    border-top-left-radius: 40px;
    box-shadow: var(--box-shadow-basic);
    display: flex;
    flex-direction: column;
    overflow: hidden;
    padding: 2rem 5%;
}

.calendar__subheader {
    letter-spacing: 0.2rem;
    margin-bottom: 1rem;
    text-align: center;
}

.calendar__subtitle {
    font-size: 1.6rem;
}

.form {
    display: flex;
    flex-direction: column;
}

.form__field {
    position: relative;
}

.form__field:focus-within .form__label {
    letter-spacing: 0.3rem;
}

.form__label {
    letter-spacing: 0.1rem;
    transition: letter-spacing var(--transition-data);
}

.form__input {
    border-bottom: 1px solid var(--black-basic);
    font-weight: 700;
    letter-spacing: 0.1rem;
    padding: 6px 0;
    transition: border-bottom var(--transition-data);
}

.form__input--invalid {
    border-bottom: 1px solid var(--red-dark);
}

.form__border {
    background: var(--black-basic);
    display: block;
    height: 2px;
    transition: width var(--transition-data);
    width: 0;
}

.form__border--invalid {
    background: var(--red-dark);
}

.form__input:focus + .form__border {
    width: 100%;
}

.form__placeholder {
    align-items: center;
    display: flex;
    justify-content: flex-end;
    min-height: 1.8rem;
}

.form__prompts {
    -webkit-animation: slide-down var(--transition-data);
            animation: slide-down var(--transition-data);
    background: var(--yellow);
    border-bottom-right-radius: 40px;
    box-shadow: var(--box-shadow-basic);
    display: grid;
    font-weight: 700;
    grid-gap: 0.25rem;
    grid-template-columns: 1fr;
    max-height: 180px;
    overflow-y: scroll;
    padding: 0.4rem 0.4rem 1rem 0.4rem;
    position: absolute;
    top: calc(80px - 1.8rem);
    -webkit-transform-origin: left top;
            transform-origin: left top;
    width: 100%;
    z-index: 1;
}

.form__suggestion {
    cursor: pointer;
    font-size: 0.8rem;
    letter-spacing: 0.1rem;
    padding: 0.4rem;
    word-break: break-all;
}

.form__suggestion--desc {
    cursor: auto;
    cursor: initial;
    font-size: 0.6rem;
    font-style: italic;
    font-weight: 300;
    letter-spacing: normal;
    letter-spacing: initial;
    padding: 0;
    text-align: right;
}

.form__err {
    color: var(--red-dark);
    font-size: 0.8rem;
}

.form__submit {
    background: var(--yellow);
    border-bottom-right-radius: 40px;
    box-shadow: var(--box-shadow-basic);
    cursor: pointer;
    font-size: 1.2rem;
    font-weight: 700;
    letter-spacing: 0.2rem;
    margin-top: 1rem;
    padding: 0.75rem;
    text-transform: lowercase;
}

@-webkit-keyframes slide-down {
    0% {
        opacity: 0;
        -webkit-transform: scaleY(0);
                transform: scaleY(0);
    }
    100% {
        opacity: 1;
        -webkit-transform: scaleY(1);
                transform: scaleY(1);
    }
}

@keyframes slide-down {
    0% {
        opacity: 0;
        -webkit-transform: scaleY(0);
                transform: scaleY(0);
    }
    100% {
        opacity: 1;
        -webkit-transform: scaleY(1);
                transform: scaleY(1);
    }
}

@media only screen and (min-width: 960px) {
    .calendar__section--form {
        margin: 0 auto;
        max-width: 1440px;
        width: 90%;
    }

    .calendar__subtitle {
        font-size: 1.8rem;
    }

    .form {
        display: grid;
        grid-gap: 1rem;
        grid-template-columns: 1fr 1fr 1fr;
    }

    .form__placeholder {
        justify-content: flex-start;
        min-height: 2.2rem;
    }

    .form__suggestion {
        font-size: 0.9rem;
        position: relative;
        max-width: -webkit-max-content;
        max-width: max-content;
    }

    .form__suggestion::after {
        background: var(--black-basic);
        border-radius: 2px;
        bottom: 5px;
        content: "";
        height: 2px;
        left: 50%;
        position: absolute;
        transition: left var(--transition-data), width var(--transition-data);
        width: 0;
    }

    .form__suggestion:hover::after {
        left: 0;
        width: 100%;
    }

    .form__suggestion--desc {
        font-size: 0.7rem;
        justify-self: end;
    }

    .form__suggestion--desc::after {
        content: none;
    }

    .form__err {
        font-size: 0.9rem;
    }

    .form__submit {
        background: var(--white-basic);
        box-shadow: var(--box-shadow-basic);
        margin: 0;
        padding: 0.85rem;
        transition: box-shadow var(--transition-data);
    }

    .form__submit:hover {
        border: none;
        box-shadow: var(--box-shadow-basic), inset 25rem 0 0 0 var(--yellow);
    }
}

.list__item {
    background: var(--white-basic);
    border-radius: 8px;
    box-shadow: var(--box-shadow-basic);
    display: grid;
    grid-gap: 10px;
    grid-template-columns: 100px minmax(0, 1fr);
    margin: 0 5%;
    padding: 1rem 0.75rem;
}

.list__card {
    background: var(--white-basic);
    border-radius: 8px;
    box-shadow: 0 0 3px var(--grey-card-shadow);
    display: flex;
    flex-direction: column;
    font-family: "Roboto", sans-serif;
    position: relative;
    text-align: center;
}

.list__card::before,
.list__card::after {
    box-shadow: 0 3px 3px -3px var(--grey-card-shadow);
    content: "";
    display: block;
    height: 100%;
    position: absolute;
}

.list__card::before {
    left: 5%;
    top: 6px;
    width: 90%;
}

.list__card::after {
    left: 2%;
    top: 3px;
    width: 96%;
}

.list__window {
    background: var(--red-dark);
    border-top-left-radius: 8px;
    border-top-right-radius: 8px;
    display: block;
    height: 20px;
    position: relative;
}

.list__window::before,
.list__window::after {
    background: var(--black-basic);
    content: "";
    display: block;
    height: 10px;
    position: absolute;
    top: -5px;
    width: 3px;
}

.list__window::before {
    left: 15%;
}

.list__window::after {
    right: 15%;
}

.list__date {
    padding: 0.5rem 0 1rem;
}

.list__day {
    font-size: 1.8rem;
    font-weight: 100;
    letter-spacing: 0.15rem;
}

.list__month {
    font-size: 0.9rem;
    letter-spacing: 0.1rem;
}

.list__time {
    padding-bottom: 0.5rem;
    padding-left: 15px;
    position: relative;
}

.list__time::before,
.list__time::after {
    content: "";
    position: absolute;
}

.list__time::before {
    background: var(--black-basic);
    display: block;
    height: 1px;
    left: 20%;
    top: -0.5rem;
    width: 60%;
}

.list__time::after {
    background-image: url(data:image/png;base64,);
    background-size: cover;
    height: 12px;
    left: 20px;
    top: 2px;
    width: 12px;
}

.list__wrapper {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.list__person {
    overflow-wrap: break-word;
}

.list__name {
    font-size: 0.9rem;
    letter-spacing: 0.1rem;
}

.list__surname {
    font-weight: 700;
    letter-spacing: 0.1rem;
    margin: 0.2rem 0 0.5rem;
}

.list__email {
    font-size: 0.8rem;
}

.list__btn {
    align-self: flex-end;
    background: var(--yellow);
    border-radius: 8px;
    box-shadow: var(--box-shadow-btn-mobile);
    color: var(--black-basic);
    font-weight: 700;
    letter-spacing: 0.1rem;
    max-width: 120px;
    min-width: 90px;
    padding: 0.25rem;
    text-transform: lowercase;
    width: 60%;
}

@media only screen and (min-width: 500px) {
    .list__item {
        grid-gap: 30px;
        padding: 1.5rem;
    }
}

@media only screen and (min-width: 680px) {
    .list__item {
        grid-gap: 10px;
        margin: 0;
        padding: 1rem 0.75rem;
    }
}

@media only screen and (min-width: 960px) {
    .list__item {
        grid-gap: 30px;
        padding: 1.5rem;
    }

    .list__surname {
        font-size: 1.2rem;
    }

    .list__email {
        font-size: 0.9rem;
    }

    .list__btn {
        background: var(--white-basic);
        border: 2px solid var(--black-basic);
        box-shadow: none;
        padding: 0.4rem;
        transition: box-shadow var(--transition-data), border var(--transition-data);
    }

    .list__btn:hover {
        border-color: var(--yellow);
        box-shadow: inset 7.5rem 0 0 0 var(--yellow);
    }
}

.modal {
    align-items: center;
    -webkit-backdrop-filter: blur(5px);
    backdrop-filter: blur(5px);
    background: var(--black-modal-layer);
    bottom: 0;
    display: flex;
    left: 0;
    padding: 0 1rem;
    position: fixed;
    right: 0;
    top: 0;
    z-index: 2;
}

.modal__content {
    align-items: center;
    background: var(--yellow);
    border-radius: 12px;
    box-shadow: var(--box-shadow-basic);
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 2rem;
    position: relative;
    width: 100%;
}

.modal__btn {
    background: transparent;
    font-size: 1.2rem;
    padding: 0.5rem;
    position: absolute;
    right: 0.5rem;
    top: 0;
}

.modal__paragraph {
    letter-spacing: 0.1rem;
    line-height: 1.5;
    margin: 1.5rem 0 2.5rem;
    text-align: center;
}

.modal__highlight {
    font-weight: 700;
}

.modal__actions {
    display: flex;
    justify-content: space-between;
    max-width: 300px;
    width: 100%;
}

.modal__confirm,
.modal__cancel {
    box-shadow: var(--box-shadow-basic);
    color: var(--white-basic);
    font-weight: 700;
    letter-spacing: 0.1rem;
    padding: 0.5rem 0.75rem;
    width: 45%;
}

.modal__confirm {
    background: var(--green-basic);
    border-top-left-radius: 40px;
}

.modal__cancel {
    background: var(--red-dark);
    border-bottom-right-radius: 40px;
}

@media only screen and (min-width: 480px) {
    .modal {
        justify-content: center;
    }

    .modal__content {
        width: 85%;
    }
}

@media only screen and (min-width: 640px) {
    .modal__content {
        width: 70%;
    }
}

@media only screen and (min-width: 800px) {
    .modal__content {
        width: 50%;
    }
}

@media only screen and (min-width: 960px) {
    .modal__confirm,
    .modal__cancel {
        background: var(--white-basic);
        transition: color var(--transition-data), box-shadow var(--transition-data);
    }

    .modal__confirm {
        color: var(--green-basic);
    }

    .modal__confirm:hover {
        box-shadow: var(--box-shadow-basic), inset 9rem 0 0 0 var(--green-basic);
        color: var(--white-basic);
    }

    .modal__cancel {
        color: var(--red-dark);
    }

    .modal__cancel:hover {
        box-shadow: var(--box-shadow-basic), inset 9rem 0 0 0 var(--red-dark);
        color: var(--white-basic);
    }
}

@media only screen and (min-width: 1250px) {
    .modal__content {
        max-width: 800px;
    }
}
.calendar__section--list {
    display: flex;
    flex-direction: column;
}

.calendar__subheader--list {
    margin-bottom: 0;
    padding: 2.5rem 5% 5rem;
}

.calendar__container {
    background: var(--white-basic);
}

.calendar__msg {
    align-items: center;
    background: var(--white-basic);
    border-radius: 8px;
    box-shadow: var(--box-shadow-basic);
    display: flex;
    justify-content: center;
    letter-spacing: 0.1rem;
    margin: -2.5rem 5% 2.5rem;
    min-height: 8rem;
    text-align: center;
}

.list {
    display: grid;
    grid-gap: 1rem;
    grid-template-columns: 1fr;
    margin: -2.5rem 0 2.5rem;
}

@media only screen and (min-width: 680px) {
    .list {
        grid-template-columns: 1fr 1fr;
        margin: -2.5rem 5% 2.5rem;
    }
}

@media only screen and (min-width: 768px) {
    .calendar__msg {
        font-size: 1.2rem;
        margin: -2.5rem 5% 2.5rem;
        max-width: 1440px;
        width: 90%;
    }
}

@media only screen and (min-width: 960px) {
    .list {
        grid-gap: 1.5rem;
        margin: -2.5rem auto 2.5rem;
        max-width: 1440px;
        width: 90%;
    }

    .calendar__msg {
        margin: -2.5rem auto 2.5rem;
        max-width: 1440px;
        width: 90%;
    }
}

.calendar {
    align-items: center;
    display: flex;
    flex-direction: column;
}

.calendar__header {
    margin: 2rem auto;
    width: 90%;
}

.calendar__pretitle {
    font-size: 1.2rem;
    font-weight: 300;
    letter-spacing: 0.1rem;
}

.calendar__title {
    font-size: 2.4rem;
    letter-spacing: 0.25rem;
    margin: 0;
}

.calendar__description {
    font-size: 1.4rem;
    letter-spacing: 0.15rem;
    margin-top: 1rem;
}

.calendar__highlight {
    -webkit-filter: blur(1.5px);
            filter: blur(1.5px);
    font-weight: 700;
    letter-spacing: 0.3rem;
}

.calendar__wrapper {
    width: 100%;
}

@media only screen and (min-width: 960px) {
    .calendar__header {
        max-width: 1440px;
        width: 90%;
    }
}

